.pricing {
  width: 100%;
  margin-top: 5rem;
}

.pricing .container {
  max-width: 1240px;
  padding: 1rem;
  margin: auto;
  overflow: hidden;
}
.pricing .top-content {
  display: grid;
  grid-template-columns: 3fr 1fr;
  border-bottom: 1px solid #d3d3d3;
  padding-bottom: 3rem;
}

.pricing .top-content h2 {
  font-size: 38px;
  padding-bottom: 0.5rem;
}
.pricing .top-content span {
  color: #6948ff;
}
.pricing .top-content p {
  color: #808080;
}

.pricing .bottom-content {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 10px;
  margin: 3rem auto;
}

.pricing .bottom-content .card {
  display: flex;
  flex-direction: column;
  padding: 1rem 1rem 1rem 0;
}
.pricing .card div {
  display: flex;
  border-bottom: 1px solid #d3d3d3;
}

.pricing .card .no-border {
  border: none;
}
.pricing .card span {
  color: #6948ff;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 14px;
}
.pricing .card h3 {
  font-size: 28px;
  margin: 0.7rem 0;
}
.card-btn {
  width: 100%;
  padding: 8px 0;
  border-radius: 4px;
  border: 1px solid #6948ff;
  background-color: #6948ff;
  color: #fff;
  cursor: pointer;
  margin-top: 40px;
}
.card-btn:hover {
  border: 1px solid #6948ff;
  background-color: #6948ff;
}
@media screen and (max-width: 960px) {
  .pricing .top-content {
    grid-template-columns: 1fr;
  }
  .pricing.container.bottom-content {
    display: none;
  }
  .pricing .top-content h2 {
    font-size: 32px;
  }
}
