.contact {
  width: 100%;
  margin: 3rem 0;
  display: flex;
}

.contact .container {
  max-width: 1240px;
  margin: auto;
  overflow: hidden;
  display: flex;
}

.contact .col-1 {
  flex: 1 0 45%;
  padding: 5rem 2rem;
}

.contact .col-1 h2 {
  font-size: 28px;
  padding: 0.4rem 0;
}

.contact .p {
  color: #808080;
}
.contact .col-1 .address {
  padding: 1rem 0;
}

.contact .col-1 .icons {
  display: flex;
  align-items: center;
  padding: 0.4rem 0;
}
.contact .careers {
  padding: 2rem 0;
}

.contact span {
  color: #6948ff;
}

.contact .col-2 {
  flex: 1 0 55%;
  padding: 5rem 2rem;
}

.contact form {
  display: flex;
  flex-direction: column;
  max-width: 500px;
  margin-left: 20px;
}

.contact form input {
  margin-bottom: 1rem;
  padding: 0.6rem;
  font-size: 1rem;
  border-radius: 4px;
  border: 1px solid #d3d3d3;
}

.contact form textarea {
  margin-bottom: 1rem;
  padding: 0.6rem;
  font-size: 1rem;
  border-radius: 4px;
  border: 1px solid #d3d3d3;
  font-family: "Montserrat", sans-serif;
}

.contact form .checkbox {
  display: flex;
  align-items: center;
  justify-content: center;
}

.contact .checkbox input {
  margin: 0;
  padding-right: 4px;
}

@media screen and (max-width: 960px) {
  .contact {
    margin: 1rem 0;
  }
  .contact .container {
    flex-direction: column;
  }
  .contact .col-1 {
    padding: 0 1rem;
  }
  .contact .col-2 {
    padding: 2rem;
  }
}
